import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { BtnExport } from 'components/common/button';
import {
  dowloadSocialStructure,
  dowloadTownStuden,
  dowloadDistrictStudent,
  dowloadForeignStudent,
  dowloadSubsidizeObjectStudent,
  dowloadPolicyObjectStudent,
} from 'services/ReportFileStudent';

import FormSearchStudent from './FormSearchStudent';

class ReceiveAdmissionPapersPage extends Component {
  state = {
    paramSearchClass: {},
  };

  dataFormSeach = data => {
    console.log('res c ci gj tng day ====>', data);
    this.setState({
      paramSearchClass: data,
    });
  };
  // C cu x hi
  dowloadFileSocialStructure = () => {
    const { paramSearchClass } = this.state;
    dowloadSocialStructure(paramSearchClass)
      .then(res => {
        console.log(res);
      })
      .catch(error => {
        console.log(error);
      });
  };
  // TK theo tnh
  dowloadFileTownStuden = () => {
    const { paramSearchClass } = this.state;
    dowloadTownStuden(paramSearchClass)
      .then(res => {
        console.log(res);
      })
      .catch(error => {
        console.log(error);
      });
  };
  // TK theo huyn th
  dowloadFileDistrictStudent = () => {
    const { paramSearchClass } = this.state;
    dowloadDistrictStudent(paramSearchClass)
      .then(res => {
        console.log(res);
      })
      .catch(error => {
        console.log(error);
      });
  };
  // Sinh vin nc ngoi
  dowloadFileForeignStudent = () => {
    const { paramSearchClass } = this.state;
    dowloadForeignStudent(paramSearchClass)
      .then(res => {
        console.log(res);
      })
      .catch(error => {
        console.log(error);
      });
  };
  // Sinh vin tr cp
  dowloadFileSubsidizeObjectStudent = () => {
    const { paramSearchClass } = this.state;
    dowloadSubsidizeObjectStudent(paramSearchClass)
      .then(res => {
        console.log(res);
      })
      .catch(error => {
        console.log(error);
      });
  };
  // Sinh vin chnh sch
  dowloadFilePolicyObjectStudent = () => {
    const { paramSearchClass } = this.state;
    dowloadPolicyObjectStudent(paramSearchClass)
      .then(res => {
        console.log(res);
      })
      .catch(error => {
        console.log(error);
      });
  };

  render() {
    return (
      <Fragment>
        <Card title="Bo co h s SV">
          <Row>
            <Col span={24}>
              <FormSearchStudent dataForm={this.dataFormSeach} />
            </Col>
            <Col span={24} style={{ marginTop: 10 }}>
              <BtnExport
                style={{ marginRight: 10 }}
                title="C cu x hi"
                onClick={() => this.dowloadFileSocialStructure()}
              />
              <BtnExport
                style={{ marginRight: 10 }}
                title="TK theo tnh"
                onClick={() => this.dowloadFileTownStuden()}
              />
              <BtnExport
                style={{ marginRight: 10 }}
                title="TK theo huyn th"
                onClick={() => this.dowloadFileDistrictStudent()}
              />
              <BtnExport
                style={{ marginRight: 10 }}
                title="Sinh vin nc ngoi"
                onClick={() => this.dowloadFileForeignStudent()}
              />
              <BtnExport
                style={{ marginRight: 10 }}
                title="Sinh vin tr cp"
                onClick={() => this.dowloadFileSubsidizeObjectStudent()}
              />
              <BtnExport
                title="Sinh vin chnh sch"
                onClick={() => this.dowloadFilePolicyObjectStudent()}
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



